import { FieldArrayBuilder } from './array';
import { FieldBuilder } from './field';
import { FieldSetBuilder } from './set';
import { FormBuilder } from './form';
export * from './array';
export * from './field';
export * from './set';
export * from './form';
export * from './basic';
/**
 *  `Field` builder
 * @param defaultValue `Field` 
 */
export function field(defaultValue) {
    return new FieldBuilder(defaultValue);
}
/**
 *  `FieldArray` builder
 * @param childBuilder  builder  `field``array`  `set` 
 */
export function array(childBuilder) {
    return new FieldArrayBuilder(childBuilder);
}
/**
 *  `FieldSet` builder
 * @param childBuilders `FieldSet`  builder  `field``array`  `set` 
 */
export function set(childBuilders) {
    return new FieldSetBuilder(childBuilders);
}
/**
 *  `Form` builder builder 
 * @param childBuilders `Form`  builder  `field``array`  `set` 
 */
export function form(childBuilders) {
    return new FormBuilder(childBuilders);
}
